.card {
  max-height: 200px;
  display: flex;
  align-items: center;
  gap: 20px;
  &:not(:last-child) {
    border-bottom: 1px solid #0000001b;
    padding-bottom: 5px;
  }

  &__info {
    flex: 0 1 75%;
    color: #000;
    @media (max-width: 400px) {
      font-size: 14px;
    }
  }
  &__img {
    flex: 0 1 15%;
    overflow: hidden;
    border-radius: 10px;
    @media (max-width: 500px) {
      flex: 0 1 17%;
    }
    img {
      border-radius: 10px;
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: all 0.4s;
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -ms-transition: all 0.4s;
      -o-transition: all 0.4s;
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      -ms-border-radius: 10px;
      -o-border-radius: 10px;
      &:hover {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -ms-transform: scale(1.1);
        -o-transform: scale(1.1);
      }
    }
  }
  &__name {
    font-weight: 500;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 10px;
    color: #000;
    transition: color 0.3s;
    -webkit-transition: color 0.3s;
    -moz-transition: color 0.3s;
    -ms-transition: color 0.3s;
    -o-transition: color 0.3s;
    &:hover {
      color: #636262;
    }
  }
  &__description {
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    color: #000;
    transition: color 0.3s;
    -webkit-transition: color 0.3s;
    -moz-transition: color 0.3s;
    -ms-transition: color 0.3s;
    -o-transition: color 0.3s;
    &:hover {
      color: #636262;
    }
  }
  &__year {
    color: #000;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__btn {
    background: none;
    font-size: 18px;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
    &:hover {
      color: #c92222;
    }
  }
  &__noFilms {
    font-size: 26px;
    margin-top: 30px;
    color: #c92222;
  }
}
